* {
  list-style: none; }

.page-header {
  margin: 0;
  padding: 0;
  background-color: #f5f5f5;
  width: 100%;
  height: 50px;
  display: flex; }
  @media (min-width: 320px) {
    .page-header {
      justify-content: space-between;
      align-items: center; } }
  .page-header__inner {
    margin: 0 auto;
    display: flex;
    padding-left: 30px;
    padding-right: 30px; }
    @media (min-width: 320px) {
      .page-header__inner {
        width: 100%;
        align-items: center;
        justify-content: space-between; } }
  @media (min-width: 320px) {
    .page-header__worktime {
      display: none; } }
  @media (min-width: 320px) {
    .page-header__logo {
      justify-content: flex-start;
      flex-grow: 1; } }

@media (min-width: 320px) {
  .main-nav {
    display: none; } }

.page {
  font-family: Montserrat, sans-serif; }
